@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.contents {
  display: flex;
}

.right_wrapper {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.right_content {
  padding: 40px 150px 0 150px;
}

input {
  border: 0;
  border-bottom: 1px solid #d3d3d3;
  outline: 0;
  margin-bottom: 20px;
  background-color: transparent;
  width: 300px;
}

.second_last {
  margin-top: 15px;
  font-size: 13px;
}

.secondd {
  display: flex;
  justify-content: space-between;
}

input[type="checkbox"] {
  width: unset;
  height: 20px;
  margin-right: 5px;
}

.finall {
  display: flex;
}

.right_content h3 {
  text-align: center;
  margin-bottom: 6px;
}

.right_content #p {
  text-align: center;
  margin-bottom: 40px;
  font-size: 14px;
}

.btn {
  width: 300px;
  padding: 6px 18px;
  background-color: #61acb3;
  color: white;
  border-radius: 20px;
  border: none;
  outline: none;
  cursor: pointer;
}

.btn-align {
  margin-top: 30px;
  text-align: center;
  margin-bottom: 100px;
}

.error {
  color: red;
  font-size: 13px;
}

@media screen and (max-width: 1190px) {
  .contents {
    flex-direction: column;
  }
  img {
    width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .right_content {
    padding: 40px 10px 0 10px;
  }
}

